body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.input-table{
  width: 100%;
}
.table {
  display: table;
  border-collapse: collapse;
}

.table .table-row {
  display: table-row;
}

.table .table-cell {
  display: table-cell;
  text-align: left;
  vertical-align: top;
  border: 1px solid black;
}